export const useUpdateExercise = () => {
    const firestore = useFireBaseFirestore();
    const [context, setContext] = useCourseContext();
    const FieldValue = useFieldValue();

    const update = (data, courseId, moduleGroupId, moduleId, exerciseId) => {
        const {content, ...rest} = data;

        console.log(rest);

        firestore.collection('Courses').doc(courseId).update({
            ['moduleGroups.' + moduleGroupId + '.modules.' + moduleId + '.exercises.' + exerciseId + '.title']: rest.title
        });

        firestore.collection('Exercises').doc(exerciseId).update({
            content: content
        });

        let temp = context;
        temp[courseId].moduleGroups[moduleGroupId].modules[moduleId].exercises[exerciseId] = {...temp[courseId].moduleGroups[moduleGroupId].modules[moduleId].exercises[exerciseId], ...rest};
        const newContext = {...temp};

        setContext(newContext);
    };

    return update;
}

const CourseProvider = ({children}) => {
    const [state, setState] = React.useState({});
    const firestore = useFireBaseFirestore();

    if(Object.entries(state).length === 0 && state.constructor === Object) {
        firestore.collection('Courses').get().then(snapshot => {
            let temp = {};

            snapshot.docs.forEach(doc => {
                temp = {...temp, [doc.id]: doc.data()}
            });

            if(!(Object.entries(temp).length === 0 && temp.constructor === Object)) {
                setState(temp);
            }
        })
    };

    return <CourseContext.Provider value={[state, setState]}>{children}</CourseContext.Provider>
};

export default CourseProvider;