import styled from "styled-components";
import React from "react";
import Icon from "./Atomics/Icon";
import Container from "./Atomics/Container";
import NavButton from "./Atomics/NavButton";

const Base = styled.div`
    width: 100%;
    position: sticky;
    bottom: 0;
    left: 0;
    z-index: 1100;
`;

const IconContainer = styled(Container)`
    > div:first-child {
        border: 0;
    }
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: #eee;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    
    > :first-child {
        border-left: none;
    }
`;

const Button = styled(NavButton).attrs(() => ({
    text: true,
    activeStyle: {
        backgroundColor: '#ccc',
    },
    exact: true,
}))`
    transition: all 0.3s ease;
    display: flex;
    padding: 8px; 
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border-left: 1px solid #ccc;
    flex: 1 1 0px;
    color: black;
    text-decoration: none;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
`;



const Description = styled.div`
    margin-top: 4px;
    font-size: 8pt;
    color: #888;
`;

const BottomBar = ({...props}) => {
    return (
        <>
            <Base>
                <IconContainer>
                    <Button to={'/forum'}>
                        <Icon>forum</Icon>
                        <Description>Forum</Description>
                    </Button>
                    <Button to={'/notes'}>
                        <Icon>notes</Icon>
                        <Description>Notizen</Description>
                    </Button>
                    <Button to={'/'}>
                        <Icon>home</Icon>
                        <Description>Start</Description>
                    </Button>
                    <Button to={'/settings'}>
                        <Icon>settings</Icon>
                        <Description>Einstellungen</Description>
                    </Button>
                    <Button to={'/feedback'}>
                        <Icon>feedback</Icon>
                        <Description>Feedback</Description>
                    </Button>
                </IconContainer>
            </Base>
        </>
    )
};

export default BottomBar;

