import React from 'react';
import {useTransition, animated} from 'react-spring';

export const useRipple = ({duration, color, size} = {
    duration: 500,
    color: 'rgba(0, 0, 0, 0.5)',
    size: 10
}, positionOverride = undefined, overrideListener = 'click') => {
    const ref = React.useRef();

    const [state, setState] = React.useState([]);

    const ripple = React.useCallback((event) => {
        const rect = ref.current.getBoundingClientRect();

        if (positionOverride) {
            setState(
                {key: uniqueId(), x: positionOverride[0], y: positionOverride[1]}
            );
        } else {
            setState(
                {key: uniqueId(), x: event.clientX - rect.x, y: event.clientY - rect.y}
            );
        }
    }, [positionOverride, state]);

    React.useEffect(() => {
        ref.current.addEventListener(overrideListener, ripple);

        const copyRef = ref;

        return () => {
            copyRef.current.removeEventListener(overrideListener, ripple);
        }
    }, [overrideListener, positionOverride, ripple]);

    const uniqueId = () => {
        let string = '';

        for (let i = 0; i < 16; i++) {
            string += String.fromCharCode(Math.floor(Math.random() * 255));
        }

        return string;
    }

    const transition = useTransition(state, item => item.key, {
        from: {transform: 'scale(0)', opacity: 1},
        enter: {transform: `scale(${size})`, opacity: 0},
        leave: {transform: `scale(${size})`, opacity: 0}
    });

    return [
        ref,
        transition.map(({item, props, key}) =>
            <animated.div
                key={item.key}
                style={{
                    ...props,
                    left: item.x,
                    top: item.y,
                    backgroundColor: color,
                    width: 10,
                    height: 10,
                    position: 'absolute',
                    borderRadius: 5
                }}
            />)
    ];
};