import React from "react";
import Button from "./Button";
import {useLocation, useHistory} from 'react-router-dom';

const NavButton = ({to, children, style, activeStyle, ...props}) => {
    const location = useLocation();
    const history = useHistory();

    const [active, setActive] = React.useState(to === location.pathname);

    const handleRedirection = (to) => {
        if(history.location.pathname !== to) history.push(to);
    };

    React.useEffect(() => {
        setActive(location.pathname === to);
    }, [location.pathname, to]);

    if(!active) activeStyle = null;

    return (
        <Button onClick={() => handleRedirection(to)} style={{...style, ...activeStyle}} {...props}>
            {children}
        </Button>
    )
};

export default NavButton;