const Forum = React.lazy(() => import("./Forum"));
const Home = React.lazy(() => import('./Home'));
const Notes = React.lazy(() => import('./Notes'));
const Profile = React.lazy(() => import("./Profile"));
const Search = React.lazy(() => import("./Search"));
const Settings = React.lazy(() => import("./Settings"));
const Course = React.lazy(() => import("./Course"));
const Module = React.lazy(() => import("./Module"));
const Exercise = React.lazy(() => import("./Exercise"));
const CMS = React.lazy(() => import('../../CMS'));

