import React from 'react';
import styled from 'styled-components';
import Container from "./Atomics/Container";
import NavButton from "./Atomics/NavButton";
import Icon from "./Atomics/Icon";
import {useSignOut} from "./Providers/FireBaseProvider";

const NavContainer = styled(Container)`
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const Base = styled.div`
    top: 0;
    left: 0;
    position: sticky;
    background-color: #0d47a1;
    color: white;
    font-size: 14pt;
    
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    z-index: 1100;
    overflow: hidden;
`;

const Button = styled(NavButton).attrs(props => ({
    text: true,
    circle: true,
    ripple: {
        duration: 500,
        color: 'rgba(255, 255, 255, 0.5)',
        size: 10,
    },
    activeStyle: {
        backgroundColor: 'rgba(255,255,255, 0.1)'
    }
}))`
    line-height: 0;
    color: white;
    padding: 8px;
    margin: 0;
`;

const NavBar = ({...props}) => {
    const signout = useSignOut();

    return (
        <Base>
            <NavContainer>
                e-Learning Plattform
                <div>
                    <Button to={'/search'}>
                        <Icon>
                            search
                        </Icon>
                    </Button>
                    <Button to={'/profile'}>
                        <Icon>
                            account_circle
                        </Icon>
                    </Button>
                    <Button onClick={signout}>
                        <Icon>
                            input
                        </Icon>
                    </Button>
                </div>
            </NavContainer>
        </Base>
    )
};

export default NavBar;