import React from 'react';
import styled from 'styled-components';
import {useRipple} from '../VisualEffects/Ripple';

const Base = styled.button`
    position: relative;
    transition: 0.5s ease background-color, 0.5s ease color;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    font-size: 12pt;
    text-align: center;
    outline: none;
    border: none;
    background-color: white;
    overflow: hidden;
    
    ${props => props.text ? `background-color: transparent; box-shadow: none;` : ``};
    ${props => props.outlined ? `background-color: transparent; box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25)` : ``};
    ${props => props.rounded ? `border-radius: 4px` : ``};
    ${props => props.circle ? `border-radius: 50%` : ``};
    ${props => props.unbounded ? `overflow: visible` : ``};
    ${props => props.styled ? `padding: 16px; color: white; background-color: #0d47a1` : ''};
`;

const Button = ({
                    children,
                    ripple = {color: 'rgba(0, 0, 0, 0.5)', duration: 500, size: 20},
                    ...props
}) => {
    const [ref, ripples] = useRipple(ripple);

    return (
        <>
            <Base ref={ref} {...props}>
                {ripples}
                {children}
            </Base>
        </>
    )
};

export default Button;